"use client"
import React, { useState } from 'react'
import { useRouter, useSearchParams } from 'next/navigation'
import API from '@/lib/api'
import ImageUploader from '@/components/ImageUploader'

export default function CreateEventPage() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const groupId = searchParams.get('groupId') || ''

  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [tags, setTags] = useState('')
  const [startsAt, setStartsAt] = useState('')
  const [endsAt, setEndsAt] = useState('')
  const [city, setCity] = useState('')
  const [venue, setVenue] = useState('')
  const [address, setAddress] = useState('')
  const [capacity, setCapacity] = useState<number | ''>('')
  const [imageUrl, setImageUrl] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const submit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError(null)
    const token = localStorage.getItem('token')
    if (!token) {
      setError('Please login first')
      setLoading(false)
      return
    }
    try {
      // For now, include tags inline in description if backend lacks a tags field
  const tagsArray = tags.split(',').map(t => t.trim()).filter(Boolean)
  const payload: any = { groupId, title, description, startsAt, city, venue, tags: tagsArray }
      if (endsAt) payload.endsAt = endsAt
      if (address) payload.address = address
      if (capacity !== '') payload.capacity = Number(capacity)
      if (imageUrl) payload.imageUrl = imageUrl

      const res = await API.post('/events', payload)
      router.push(`/events/${res.data.event.id}`)
    } catch (err: any) {
      setError(err?.response?.data?.error || 'Failed to create event')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-black via-purple-900/20 to-black p-6 flex items-center justify-center">
      <div className="w-full max-w-2xl relative overflow-hidden rounded-3xl bg-white/10 p-8 shadow-2xl backdrop-blur-xl border border-white/20">
        <div className="absolute inset-0 bg-gradient-to-br from-purple-500/10 to-pink-500/10 -z-10" />
        <h2 className="mb-6 text-2xl font-bold text-white">Create an Event</h2>
        <form onSubmit={submit} className="space-y-5">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Group ID</label>
              <input value={groupId} readOnly className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white" />
            </div>
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Event Title</label>
              <input value={title} onChange={(e) => setTitle(e.target.value)} placeholder="Hackathon Night" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" required />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Starts At</label>
              <input type="datetime-local" value={startsAt} onChange={(e) => setStartsAt(e.target.value)} className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white" required />
            </div>
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Ends At</label>
              <input type="datetime-local" value={endsAt} onChange={(e) => setEndsAt(e.target.value)} className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white" />
            </div>
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-300">Description</label>
            <textarea value={description} onChange={(e) => setDescription(e.target.value)} placeholder="What is this event about?" rows={4} className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-5">
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">City</label>
              <input value={city} onChange={(e) => setCity(e.target.value)} placeholder="Mumbai" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" required />
            </div>
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Venue</label>
              <input value={venue} onChange={(e) => setVenue(e.target.value)} placeholder="Tech Hub" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" />
            </div>
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Address</label>
              <input value={address} onChange={(e) => setAddress(e.target.value)} placeholder="Street, Area" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-5">
            <div>
              <label className="mb-2 block text-sm font-medium text-zinc-300">Capacity</label>
              <input type="number" min={1} value={capacity} onChange={(e) => setCapacity(e.target.value === '' ? '' : Number(e.target.value))} placeholder="100" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" />
            </div>
            <div className="md:col-span-2">
              <label className="mb-2 block text-sm font-medium text-zinc-300">Tags</label>
              <input value={tags} onChange={(e) => setTags(e.target.value)} placeholder="AI, Web, Hackathon" className="w-full rounded-xl border border-white/20 bg-white/5 py-3 px-4 text-white placeholder:text-zinc-400" />
              <p className="mt-1 text-xs text-zinc-400">Comma-separated tags</p>
            </div>
          </div>

          <div>
            <label className="mb-2 block text-sm font-medium text-zinc-300">Poster</label>
            <ImageUploader value={imageUrl} onChange={setImageUrl} />
          </div>

          <div className="flex justify-end gap-3">
            <button type="button" onClick={() => router.back()} className="rounded-xl border border-white/20 bg-white/5 px-5 py-2.5 text-white hover:bg-white/10">Cancel</button>
            <button disabled={loading} className="rounded-xl bg-gradient-to-r from-purple-500 to-pink-600 px-6 py-2.5 font-semibold text-white hover:shadow-purple-500/50 disabled:opacity-50">
              {loading ? 'Creating...' : 'Create Event'}
            </button>
          </div>

          {error && <div className="mt-3 text-sm text-red-400">{error}</div>}
        </form>
      </div>
    </div>
  )
}
